@font-face {
    font-family: "Plaid M Mono";
    font-style: normal;
    font-weight: 400;
    font-display: auto;
    src: url('/fonts/Plaid/Plaid-M-Mono.woff2') format('woff2'),
    url('/fonts/Plaid/Plaid-M-Mono.woff') format('woff');
    unicode-range: U+0000-00FF, U+0131, U+0152-0153, U+02BB-02BC, U+02C6, U+02DA,
    U+02DC, U+2000-206F, U+2074, U+20AC, U+2122, U+2191, U+2193,
    U+2212, U+2215, U+FEFF, U+FFFD;
}

